"use client";
import { useEffect, useState } from "react";
import Image from "next/image";

export default function Nav() {
  const [solid, setSolid] = useState(false);

  useEffect(() => {
    const onScroll = () => setSolid(window.scrollY > 60);
    onScroll();
    window.addEventListener("scroll", onScroll);
    return () => window.removeEventListener("scroll", onScroll);
  }, []);

  return (
    <header
      className={`fixed inset-x-0 top-0 z-50 transition-colors ${
        solid
          ? "bg-black/70 backdrop-blur border-b border-white/10"
          : "bg-transparent"
      }`}
    >
      <div className="container flex items-center justify-between py-3">
        {/* Logo */}
        <a href="#inicio" className="flex items-center gap-3">
          <Image
            src="/logo.png"
            alt="Ciudad Avivamiento"
            width={36}
            height={36}
            priority
            className="rounded-full"
          />
          <strong className="hidden sm:block">Ciudad Avivamiento</strong>
        </a>

        {/* Navegación principal */}
        <nav className="hidden lg:flex gap-1">
          <a className="nav-link" href="#about">SOMOS AVIVAMIENTO</a>
          <a className="nav-link" href="#nosotros">NOSOTROS</a>
          <a className="nav-link" href="#live">LIVE</a>
          <a className="nav-link" href="#pasos">PRÓXIMOS PASOS</a>
          <a className="nav-link" href="#servicios">SERVICIOS</a>
          <a className="nav-link" href="#donar">DONAR</a>
        </nav>

        {/* Iconos (buscar / perfil) */}
        <div className="flex items-center gap-3">
          <button aria-label="Buscar" className="nav-link">
            <svg
              width="18"
              height="18"
              viewBox="0 0 24 24"
              fill="none"
            >
              <circle cx="11" cy="11" r="7" stroke="currentColor" strokeWidth="2" />
              <path
                d="M20 20L17 17"
                stroke="currentColor"
                strokeWidth="2"
                strokeLinecap="round"
              />
            </svg>
          </button>
          <button aria-label="Perfil" className="nav-link">
            <svg
              width="18"
              height="18"
              viewBox="0 0 24 24"
              fill="none"
            >
              <circle cx="12" cy="8" r="4" stroke="currentColor" strokeWidth="2" />
              <path
                d="M5 20c1.5-3 4.5-5 7-5s5.5 2 7 5"
                stroke="currentColor"
                strokeWidth="2"
                strokeLinecap="round"
              />
            </svg>
          </button>
        </div>
      </div>
    </header>
  );
}



